.main {
    display: flex;
    flex-direction: row;
}

.canvas {
    width: 50%;
    padding-top: 30px;
}

textarea {
    font-family: "Euclid Circular A", sans-serif;
    border: 1.5px solid #EBEAEA;
    border-radius: 5px;
    padding: 15px;
    width: calc(100% - 33px);
}

textarea::placeholder {
    font-family: "Euclid Circular A", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #B8B8B7;
    letter-spacing: -0.06em;
}

.data {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 30px;
    width: 50%;
}

.headerWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 0 15px;
    gap: 10px;
    width: 100%;
    border-bottom: 1px solid #EBEAEA;
    justify-content: space-between;
}

.headerInnerWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;
}


.personalImage {
    text-align: center;
}
.personalImage input[type="file"] {
    display: none;
}
.personalFigure {
    margin: 0;
    position: relative;
    width: 80px;
    height: 80px;
}
.personalAvatar {
    cursor: pointer;
    width: 80px;
    height: 80px;
    box-sizing: border-box;
    border-radius: 100%;
    border: 2px solid transparent;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
    transition: all ease-in-out .3s;
    object-fit: cover;
}
.personalAvatar:hover {
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
}
.personalFigcaption {
    cursor: pointer;
    position: absolute;
    top: 0;
    width: inherit;
    height: inherit;
    border-radius: 100%;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0);
    transition: all ease-in-out .3s;
}
.personalFigcaption:hover {
    opacity: 1;
    background-color: rgba(0, 0, 0, .5);
}
.personalFigcaption > img {
    margin-top: 24px;
    width: 25px;
    height: 25px;
}





.innerWrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    width: 100%;
    padding-top: 15px;
}

.innerWrapper h1 {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 23px;
    letter-spacing: -0.06em;
    margin: 0;
    color: #222222;
}

.innerWrapper h2 {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: -0.06em;
    color: #787878;
    margin: 0;
}

.dataWrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    gap: 15px;
    width: 100%;
}

.buttonPanel {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 15px;
    border-top: 1px solid #EBEAEA;
    padding: 15px 30px;
    position: fixed;
    bottom: 0;
    background-color: #FFFFFF;
    width: 100%;

}

.progressBar {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 50px;
    height: 50px;
    border-radius: 50%;
    transition: all 0.3s;
    background:
            radial-gradient(closest-side, white 79%, transparent 80% 100%),
            conic-gradient(#F7634A 75%, white 0);
}

.progressBar::before {
    content: attr(data-percentage);
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: -0.06em;
    color: #000000;
}


.formWrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    gap: 30px;
    width: 100%;
}

.formInnerWrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    gap: 15px;
    width: 100%;
}

.inputWrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    gap: 15px;
    width: 100%;
}

.label {
    font-style: normal;
    font-weight: 400;
    letter-spacing: -0.06em;
    font-size: 16px;
    line-height: 20px;
    color: #787878;
}

.dateWrapper {
    display: flex;
    width: 100%;
    align-items: center;
    gap: 15px;
}

.input[type=date] {
    font-family: "Euclid Circular A", sans-serif;
    color: #787878;
}

.input {
    height: 47px;
    border: 1.5px solid #EBEAEA;
    border-radius: 5px;
    padding: 0 15px;
    width: calc(100% - 33px);
}

input::placeholder {
    font-family: "Euclid Circular A", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.06em;
    color: #B8B8B7;
}

.input:focus {
    background: #F2F2F2;
    border: 1.5px solid #EBEAEA;
}

input:focus-visible {
    outline: unset;
}




/*Accordion */

.tabLabelInner {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0;
    gap: 10px;
    width: 100%;
}

.tabLabelWrapper {
    display: flex;
    align-items: center;
    width: 100%;
    padding-right: 15px;
}

.tabTitle {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.06em;
    color: #222222;
}

.tabDescription {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    display: flex;
    align-items: flex-end;
    letter-spacing: -0.06em;
    color: #787878;

}

.tabLabelInfo {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0;
    gap: 5px;
}

input[type=checkbox] {
    position: absolute;
    opacity: 0;
    z-index: -1;
}

.tabs {
    overflow: hidden;
    width: 100%;
}

.tab {
    width: 100%;
    font-family: 'Euclid Circular A', serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.06em;
    color: #222222;
    overflow: hidden;
}
.tabLabel {
    display: flex;
    justify-content: space-between;
    background: #FFFFFF;
    cursor: pointer;
    align-items: center;
}

.tabLabel::after {
    content: "❯";
    width: 1em;
    height: 1em;
    text-align: center;
    transition: all 0.35s;
}
.tabContent {
    max-height: 0;
    padding-top: 15px;
    background: white;
    transition: all 0.35s;
}



input:checked + .tabLabel::after {
    transform: rotate(90deg);
}
input:checked ~ .tabContent {
    max-height: 100vh;
    padding-bottom: 15px;
}


.addWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0;
    gap: 10px;
    border: none;
    background: transparent;
}

.addWrapper span {
    font-family: "Euclid Circular A", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.04em;
    color: #F7634A;
}

.downloadLink {
    transition: 0.3s all;
}

.downloadLink:hover {
    color: #F7634A;
}

@media (min-width: 986px) and (max-width: 9999px) {
    .buttonPanel {
        position: unset;
        padding: 0;
        border: none;
        width: unset;
    }

    .innerWrapper h1 {
        font-weight: 600;
        font-size: 24px;
        line-height: 30px;
    }

    .innerWrapper h2 {
        font-size: 14px;
        line-height: 18px;
    }

    .headerInnerWrapper span {
        font-weight: 500;
        font-size: 20px;
        letter-spacing: -0.06em;
    }
}


.skillInput {
    height: 47px;
    border: 1.5px solid #222222;
    border-radius: 5px;
    padding-left: 15px;
    padding-right: 35px;
    background-size: 30px;
    background: url("../img/iconPlus.svg") no-repeat right 5px center;
    width: calc(100% - 53px);
}

.skillInput:focus {
    background-color: #F2F2F2;
}

.canvasMobile {
    display: none;
}

@media screen and (max-width: 985px) {
    main {
        display: flex;
    }

    .data {
        width: 100%;
        padding: 0;
    }

    .canvas {
        display: none;
    }

    .canvasMobile {
        display: unset;
    }
}
